.TodosDeletes-Container{
    width: 90%;
    max-width: 500px;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    padding: 10px;
    border-radius: 10px;
}
.inputDeletes{
 background: #F9FBFC;
  border-radius: 2px;
  border: 2px solid #202329;
  margin: 0 24px;
